@import 'variables';
@import 'mixins';

/* ==========================================================================
   #Comments styles - start
   ========================================================================== */

.qodef-comment-holder {
    @include qodefRelativeHolderLayout();
    padding: 0;
    box-sizing: border-box;

    .qodef-comment-holder-inner {
        padding: 34px 0 20px 2px;
    }

    .qodef-comment {

        &.qodef-pingback-comment {

            .qodef-comment-text {
                padding: 0;
            }
        }
    }

    .qodef-comment-list {
        position: relative;
        padding: 0;
        margin: 0;
        list-style: none;
        z-index: 150;

        li {
            @include qodefRelativeHolderLayout();
            margin: 8px 0 0;
            padding: 29px 0 0;
        }

        > li:first-child {
            margin: 0;
            padding: 0;
            border: 0;
        }

        .children {
            padding: 0 0 0 110px;
        }
    }

    .qodef-comments-title {
        margin-bottom: 11px;

        > * {
            margin: 0;
        }
    }

    .qodef-comment-image {
        position: relative;
        top: 10px;
        left: 0;
        width: 82px;
        height: 82px;
        display: block;
        float: left;

        img {
            display: block;
            border-radius: 4em;
        }
    }

    .qodef-comment-text {
        padding: 0 0 0 108px;
        min-height: 82px;

        .qodef-comment-info {
            @include qodefRelativeHolderLayout();
            margin: -7px 0 7px;
        }

        .replay,
        .comment-reply-link,
        .comment-edit-link {
            float: right;
            margin: 15px 0 0 10px;
            font-size: 16px;
            color: $default-heading-color;
            font-weight: 700;

            &:hover {
                color: rgba($default-heading-color, .8);
            }
        }

        .qodef-comment-name {
            float: left;
            margin: 0;
        }

        .qodef-comment-date {
            float: left;
            margin: 7px 0 0 0;
            font-size: 15px;
        }

        .qodef-text-holder p {
            margin: 5px 0 5px 5px;
        }

        .qodef-text-holder blockquote p {
            margin: 30px 100px 0 90px;
        }

        .comment-respond {
            margin: 20px 0;

            .comment-reply-title {
                margin: 0 0 10px;
                font-size: 14px;
            }

            .logged-in-as {
                margin: 0 0 8px;
            }
        }

        #cancel-comment-reply-link {
            margin-left: 10px;
            font-size: 12px;
            color: $first-main-color;

            &:hover {
                color: $default-heading-color;
            }
        }

        ul {
            list-style-type: disc;

            ul {
                list-style-type: circle;

                ul {
                    list-style-type: square;
                }
            }
        }

        ul, ol {

            li {
                display: list-item;
            }
        }

        li {
            margin: 0 !important;
            padding: 0 !important;
            border: 0 !important;
        }
    }
}

.qodef-comment-form {
    padding: 0;
    clear: both;
    box-sizing: border-box;

    .qodef-comment-form-inner {
        padding: 57px 0 78px;
    }

    .comment-reply-title {
        margin: 0 0 34px;
    }

    .logged-in-as {
        margin: 0 0 8px;
    }
}

@include ipad-landscape {

    .qodef-comment-holder {

        .qodef-comment-list {

            .children {
                padding: 0 0 0 80px;
            }
        }
    }
}

@include ipad-portrait {

    .qodef-comment-holder {

        .qodef-comment-list {

            .children {
                padding: 0 0 0 40px;
            }
        }
    }
}

@include phone-landscape {

    .qodef-comment-holder {

        .qodef-comment-list {

            .children {
                padding: 0;
            }
        }

        .qodef-comment-text {

            .qodef-comment-date,
            .qodef-comment-name {
                float: none;
            }

            .replay,
            .comment-reply-link,
            .comment-edit-link {
                float: none;
                margin: 0 10px 0 0;
            }
        }
    }
}

@include phone-portrait {

    .qodef-comment-holder {

        .qodef-comment-image {
            float: none;
        }

        .qodef-comment-text {
            padding: 20px 0 0;
            min-height: inherit;
        }
    }
}

/* ==========================================================================
   #Comments styles - end
   ========================================================================== */

#respond {
    textarea {
        min-height: 185px;
        padding: 15px 0px 12px 25px;
        color: $default-text-color;
        font-weight: 400;
    }

    #author,
    #email {
        font-size: 16px;
        font-weight: 700;
        width: 48.8%;
        float: left;
        padding: 12px 0 0 0;
        border-top: none;
        border-left: none;
        border-right: none;
    }

    #email {
        float: right;
    }

    #url {
        font-size: 16px;
        font-weight: 700;
        margin: 0 0 16px;
        padding: 5px 0 0 0;
        border-top: none;
        border-left: none;
        border-right: none;
    }
}

#respond textarea,
.qodef-style-form textarea,
#respond input[type='text'],
input[type='text'],
input[type='email'],
input[type='password'],
.post-password-form input[type='password'] {
    @extend %input-style;
}

.post-password-form input[type='password'] {
    margin-top: 10px;
}

.wpcf7-form-control {
    &.wpcf7-text,
    &.wpcf7-number,
    &.wpcf7-date,
    &.wpcf7-textarea,
    &.wpcf7-select,
    &.wpcf7-quiz {
        @extend %input-style;

        footer & {
            margin: 0;
        }
    }
}

#submit_comment {
    width: 100%;
	
	//Need to override general as it doesn't have the same space for some reason
	.qodef-btn-text {
		@include qodefTransform(translate3d(-10px, 0, 0));
	}
	
	&:hover {
		.qodef-btn-text {
			@include qodefTransform(translateX(0));
		}
	}
}

//#submit_comment, // added our button classes in comments.php
//input.wpcf7-form-control.wpcf7-submit, // added our button classes in contact-form-7-confing.php
.post-password-form input[type='submit']{
    @include qodefButtonDefaultStyle();
    @include qodefButtonSize();
    @include qodefButtonSolidColor();

    cursor: pointer;

    &:hover {
        @include qodefButtonSolidHoverColor();
    }
}

p.form-submit,
.wpcf7-form p {
    margin: 20px 0 0 0;
}

#respond textarea,
.qodef-style-form textarea,
.wpcf7-form textarea {
    display: block;
}

.qodef-two-columns-form-without-space {
    .wpcf7-form .ajax-loader {
        display: none;
    }
}

/* ==========================================================================
   #Comments styles - end
   ========================================================================== */

/* ==========================================================================
   #Global Owl Slider - begin
   ========================================================================== */
.qodef-owl-slider {
    visibility: hidden;

    .owl-stage-outer {
        z-index: 1;
    }

    &.qodef-slider-is-moving {
        .owl-item {
            .qodef-block-drag-link {
                z-index: -1 !important;
            }
        }
    }

    &.qodef-slider-has-both-nav {
        .owl-nav {
            .owl-prev,
            .owl-next {
                $pos: calc(-50% - 21px); // 21 is is half height of pagination control + top margin
                @include qodefTransform(translateY($pos));
            }
        }
    }

    &.qodef-slider-hide-pagination .owl-dots {
        display: none;
    }

    .owl-nav {

        .owl-prev,
        .owl-next {
            position: absolute;
            top: 50%;
            padding: 0;
            margin: 0;
            color: $default-heading-color;
            background: none;
            border: 0;
            border-radius: 0;
            outline: none;
            -webkit-appearance: none;
            z-index: 3;

            @include qodefTransform(translateY(-50%));
            @include qodefTransition(color .2s ease-out);

            &:hover {
                color: $first-main-color;
            }

            > span {
                position: relative;
                display: inline-block;
                vertical-align: middle;
                font-size: 46px;
                line-height: 1;

                &:before {
                    display: block;
                    line-height: inherit;
                }
            }
        }

        .owl-prev {
            left: 30px;
        }

        .owl-next {
            right: 30px;
        }
    }

    .owl-dots {
        text-align: center;
        margin: 16px 0 0;
        counter-reset: section;

        .owl-dot {
            display: inline-flex;
            vertical-align: middle;
	        -webkit-align-items: center;
	        align-items: center;
            padding: 0;
            margin: 0 6px;
            background: none;
            border: 0;
            border-radius: 0;
            outline: none;
            -webkit-appearance: none;
            overflow: visible;
	
	        &:before {
		        content:'';
		        flex: 1 0 auto;
		        position: relative;
		        height: 1px;
		        width: 0;
		        margin-right: 10px;
		        background: currentColor;
		        @include qodefTransition(width .25s ease)
	        }

            &:after {
                content: "0" counter(section);
                position: relative;
                display: block;
                font-size: 16px;
                counter-increment: section;
                font-weight: 700;
                font-family: $default-text-font;
                @include qodefTransition(transform 0.2s ease-out);
            }

            &:hover,
            &.active {

                &:after {
                    @include qodefTransform(scale(1.4, 1.4));
                    @include qodefTransition(transform 0.2s ease-in);
                    font-size: 16px;
                }
            }

            &.active {

                &:before {
                    width: 100px;
                }
                
                &:after {
                    pointer-events: none;
                    cursor: default;
                }
            }
        }
    }

    .owl-carousel .owl-item img {
        -webkit-transform: translateZ(0); //chrome text cut off fix
    }

    + .qodef-slider-thumbnail {
        display: inline-block;
        vertical-align: middle;
        list-style: none;
        margin: 20px -10px 0;

        > .qodef-slider-thumbnail-item {
            float: left;
            padding: 0 10px;
            margin-bottom: 20px;
            box-sizing: border-box;
            line-height: 0;
            cursor: pointer;

            img {
                border: 2px solid transparent;

                @include qodefTransition(border-color 0.2s ease-out);
            }

            &.active {
                img {
                    border: 2px solid $first-main-color;
                }
            }
        }

        $thumbnail_columns_number: ('two', 'three', 'four', 'five', 'six');

        @for $i from 0 to length($thumbnail_columns_number) {
            &.qodef-slider-columns-#{nth($thumbnail_columns_number,$i+1)} {

                .qodef-slider-thumbnail-item {
                    width: 100% / ($i+2);

                    @include ipad-landscape {
                        width: 33.3333%;
                    }
                }
            }
        }
    }
}

/* ==========================================================================
   #Global Owl Slider - end
   ========================================================================== */

/* ==========================================================================
   #404 styles - start
   ========================================================================== */

.error404 {

    .qodef-wrapper {
        padding: 0 !important;
    }

    .qodef-content {
        height: 100vh;
    }

    &.admin-bar {
        .qodef-content {
            height: calc(100vh - 32px);
        }
    }

    .qodef-content-inner {
        @include qodefTableLayout();
    }

    .qodef-page-not-found {
        @include qodefTableCellLayout();

        padding: 0 20%;
        text-align: center;

        .qodef-404-title {
            margin: 0;
        }

        .qodef-404-subtitle {
            margin: 35px 0 0;
        }

        .qodef-404-text {
            margin: 10px 0 0;
        }

        .qodef-btn {
            margin: 30px 0 0;

            &.qodef-btn-light-style {
                color: #585858;
                background-color: #fff;
                border-color: #fff;

                &:hover {
                    color: #fff !important;
                    background-color: transparent !important;
                    border-color: #fff !important;
                }
            }
        }
    }

    @include ipad-landscape {

        .qodef-content {
            height: 100%;
            min-height: 100vh;

            .qodef-page-not-found {
                padding: 25% 20%;
            }
        }

        &.admin-bar {

            .qodef-content {
                height: 100%;
                min-height: calc(100vh - 32px);
            }
        }
    }

    @include ipad-portrait {

        .qodef-content {

            .qodef-page-not-found {
                padding: 50% 20%;
            }
        }
    }
}

/* ==========================================================================
   #404 styles - end
   ========================================================================== */

/* ==========================================================================
   #Back to Top styles - start
   ========================================================================== */
#qodef-back-to-top {
    position: fixed;
    width: 41px;
    right: -17px;
    bottom: 25px;
    margin: 0;
    z-index: 10000;
    opacity: 0;
    visibility: hidden;
	@include qodefTransformOrigin(50%);
	@include qodefTransform(rotate(-90deg));

    &.off {
        opacity: 0;
        visibility: hidden;

        @include qodefTransition(opacity 0.15s ease, visibility .0s .15s);
    }

    &.on {
        opacity: 1;
        visibility: visible;

        @include qodefTransition(opacity 0.3s ease);
    }
	
	&:hover {
		color: #000
	}

    &.qodef-back-to-top-light {
        color: #fff;
        &:hover {
            color: #fff
        }
    }
}

/* ==========================================================================
   #Back to Top styles - end
   ========================================================================== */

/* ==========================================================================
