/* ==========================================================================
   #Custom Contact Form 7 - start
   ========================================================================== */

.qodef-cf7-newsletter,
.qodef-cf7-contact,
.qodef-cf7-newsletter-2 {

    input[type='email'],
    input[type="text"] {
        border: none;
        border-bottom: 1px solid #bababa;
        padding: 0 0 2px;
        line-height: 20px;
        margin: 0px 0 41px;
        cursor: auto;

        @include qodefPlaceholder {
            font-size: 17px;
            font-weight: 500;
        }
    }

    textarea.wpcf7-form-control {
        border: 1px solid #bababa;
        padding: 15px 23px 11px;
        min-height: 68px;
        margin: 0px 0 7px;

        @include qodefPlaceholder {
            color: #7d7d7d;
            font-size: 16px;
        }
    }
}

.qodef-cf7-contact {

    input[type='email'],
    input[type="text"] {
        padding: 0 0 3px;
        margin: 0 0 22px;
    }

    input[type="text"] {
        margin: 0 0 35px;
    }
	
	.qodef-btn {
		width: 100%;
	}
}

.qodef-cf7-newsletter-2 {
    @include qodefRelativeHolderLayout();

    input[type="email"] {
        padding: 0 0 5px;
        margin: 0;
    }

    button[type="submit"].wpcf7-form-control {
        position: absolute;
        background-color: transparent;
        padding: 0;
        font-weight: 300;
        font-size: 14px;
        line-height: 14px;
        border: none;
        outline: none;
        width: 14px;
        height: 14px;
        top: 4px;
        right: 6px;

        &:hover {
            background-color: transparent !important;
        }

        &:not(.qodef-btn-icon):not(.qodef-btn-simple):not(.qodef-no-arrow-btn) {

            .qodef-btn-text {
                font-family: dripicons-v2;
                color: $default-heading-color;
                @include qodefTransform(translate3d(0, 0, 0) !important);

                &:after {
                    content: '';
                    margin: 0;
                }
            }
        }
    }
}

.wpcf7-form .qodef-btn {
	
	//Need to override general as it doesn't have the same space for some reason
	.qodef-btn-text {
		@include qodefTransform(translate3d(-10px, 0, 0) !important);
	}
	
	&:hover {
		.qodef-btn-text {
			@include qodefTransform(translateX(0) !important);
		}
	}
}

/* ==========================================================================
   #Custom Contact Form 7 - end
   ========================================================================== */

/* ==========================================================================
   #Before Text & Row Text - start
   ========================================================================== */

.qodef-before-text {

    &:before {
        @include qodefBeforeText();
    }
}

.qodef-row-text-holder {
    @include qodefRelativeHolderLayout();
    margin: 0 0 171px 0;

    .qodef-row-text {
        @include qodefRowText();
    }
}

@include ipad-landscape {
     .qodef-row-text-holder {
         margin: 0 0 150px 0;

         .qodef-row-text {
             font-size: 280px;
         }
     }
 }

@include ipad-portrait {
    .qodef-row-text-holder {
        margin: 0 0 130px 0;

        .qodef-row-text {
            font-size: 230px;
        }
    }
}

@include phone-portrait {
    .qodef-row-text-holder {
        margin: 0 0 70px 0;

        .qodef-row-text {
            font-size: 120px;
            letter-spacing: -5px;
        }
    }
}

/* ==========================================================================
   #Before Text & Row Text - end
   ========================================================================== */


.qodef-aarhus-burger {

    rect {
        @include qodefTransition(.3s);
    }

    &:hover {

        rect {

            &:nth-child(2), &:nth-child(3), &:nth-child(4) {
                @include qodefTransform(translate3d(0, 9px, 0));
            }

            &:nth-child(8), &:nth-child(9), &:nth-child(10) {
                @include qodefTransform(translate3d(0, -9px, 0));
            }
        }
    }

    .qodef-fullscreen-fade-in & {
        
        rect {

            &:nth-child(2), &:nth-child(3), &:nth-child(4) {
                @include qodefTransform(translate3d(0, 9px, 0));
            }

            &:nth-child(8), &:nth-child(9), &:nth-child(10) {
                @include qodefTransform(translate3d(0, -9px, 0));
            }
        }
    }
}


/* ==========================================================================
   #Row Background styles - begin
   ========================================================================== */
.qodef-row-has-background-text {
    position: relative;
}

.qodef-row-background-text-holder {
    @include qodefAbsoluteHolderLayout();

    .qodef-row-background-text-wrapper{
        @include qodefTableLayout();

        .qodef-row-background-text-wrapper-inner{
            display: table-cell;
            vertical-align: middle;
            color: #f7f7f7;
            font-size: 150px;

            .qodef-row-background-text-1,
            .qodef-row-background-text-2{
                font-weight: 900;
                line-height: 1;
            }
        }

        @include ipad-landscape{
            display: none;
        }

        &.qodef-row-background-text-animation {
            visibility: hidden;

            .no-touch & {
                .qodef-char-mask {
                    display: inline-block;
                    vertical-align: middle;
                    overflow: hidden;

                    > span {
                        display: inline-block;
                        @include qodefTransform(translate3d(-100%,0,0));
                        @include qodefTransition(all .5s cubic-bezier(0.05, 0.43, 0.04, 0.87));
                    }
                }

                &.qodef-row-background-text-align-right {
                    .qodef-char-mask > span {
                        @include qodefTransform(translate3d(100%,0,0));
                    }
                }

                .qodef-char-mask > span.qodef-show {
                    @include qodefTransform(translate3d(0,0,0));
                }
            }
        }
    }
}

/* ==========================================================================
   #Row Background styles - end
   ========================================================================== */

