/* ==========================================================================
   Sidebar area styles - begin
   ========================================================================== */

aside.qodef-sidebar {
    position: relative;
    z-index: 11;
    margin: 0 0 60px;

    .widget {

        &:last-child {
            margin: 0 !important;
        }

        &.widget_qodef_instagram_widget {

            .qodef-widget-title-holder {

                .qodef-widget-title {
                    margin: 0 0 23px;
                }
            }

            a {

                img {
                    @include qodefTransition(transform .5s ease-out);
                }

                &:hover {

                    img {
                        @include qodefTransform(scale(1.03, 1.03));
                        @include qodefTransition(transform .5s ease-in);
                    }
                }

                &:after {
                    background-color: transparent;
                }
            }
        }
	}

    .qodef-icon-widget-holder {
        display: block;
    }
}

.wpb_widgetised_column {
    position: relative;

    .widget {
	
	    &:last-child {
		    margin: 0 !important;
	    }
    }
}
/* ==========================================================================
   Sidebar area styles - end
   ========================================================================== */

/* ==========================================================================
   Default WordPress Widgets styles - begin
   ========================================================================== */

.widget {
	margin: 0 0 20px;
	
	.qodef-widget-title {
		margin: 0 0 21px;
		
		a {
			color: inherit;
		}
	}
	
	ul {
		padding: 0;
		margin: 0;
		list-style: none;
		
		li {
			margin: 0 0 10px;
			
			&:last-child {
				margin: 0;
			}
		}
	}

    /*
    *   Specific styles
    */
    &.widget_text {

        img {
            display: block;
        }
    }

    #wp-calendar {
        width: 100%;
        text-align: center;

        caption {
            color: $default-heading-color;
            margin: 0 0 15px;
        }

        th, td {
            padding: 3px 4px;
        }

        td#today {
            color: $default-background-color;
            background-color: $first_main_color;

            a {
                color: inherit;
            }
        }
    }

    &.widget_rss {

        .qodef-widget-title {

            .rsswidget {

                &:hover {
                    color: $first-main-color;
                }
            }
        }

        .rss-widget-icon {
            display: inline-block;
        }

        ul {

            li {
                margin: 0 0 25px;

                .rss-date {
                    display: block;
                }
            }
        }
    }

    &.widget_search {

        form {
            @include qodefRelativeHolderLayout();
            clear: both;

            > div {
                @include qodefRelativeHolderLayout();
                border-top: none;
                border-left: none;
                border-right: none;
                border-bottom: 1px solid #bababa;
                box-sizing: border-box;

                .search-field {

                    @include qodefPlaceholder {
                        font-weight: 700;
                        font-size: 16px;
                    }
                }
            }
        }

        input,
        button {
            position: relative;
            display: inline-block;
            vertical-align: top;
            float: left;
            height: 25px;
            font-family: inherit;
            font-weight: 400;
            font-size: 19px;
            line-height: 25px;
            color: $default-heading-color;
            background-color: transparent;
            border: 0;
            border-radius: 0;
            outline: 0;
            -webkit-appearance: none;
            box-sizing: border-box;
            @include qodefTransition(color .2s ease-out);
        }

        input {
            width: calc(100% - 40px);
	
	        @include qodefPlaceholder {
		        color: inherit;
	        }
        }

        button {
            padding: 0 5px 0 0;
            font-size: 13px;
            color: $default-heading-color;
            cursor: pointer;
            @include qodefTransition(color .2s ease-in-out);

            &:hover {
                color: $default-text-color;
            }

            span {
                display: block;
                line-height: inherit;

                &:before {
                    display: block;
                    line-height: inherit;
                }
            }
        }
    }

    &.qodef-blog-list-widget {

        .qodef-blog-list {

            .qodef-post-image {

                a {
                    overflow: hidden;

                    &:hover {

                        img {
                            @include qodefTransform(scale(1.03, 1.03));
                        }
                    }

                    img {
                        @include qodefTransition(transform .5s ease-out);
                    }

                    &:after {
                        background-color: transparent;
                    }
                }
            }

            .qodef-bli-content {
                vertical-align: middle;
                padding-left: 16px;

                .qodef-post-title {

                    a {

                        &:hover {
                            color: #393939;
                        }
                    }
                }

                .qodef-post-info-date {
                    margin: 0;

                    a {
                        font-size: 15px;
                        line-height: 25px;
                        letter-spacing: 0;
                    }
                }
            }
        }
    }

    &.widget_categories {

        .qodef-widget-title {
            margin: 0 0 10px;
        }

        li.cat-item {
            padding: 5px 0;

            a {
                color: $default-text-color;

                &:hover {
                    color: $default-heading-color;
                }
            }
        }
    }

    &.qodef-social-icons-group-widget {

        .qodef-widget-title {
            margin: 0 0 19px;
        }

        .qodef-social-icon-widget-holder {
            margin: 0 18px 0 0;
        }
    }

    &.widget_pages,
    &.widget_archive,
    &.widget_categories,
    &.widget_meta,
    &.widget_recent_comments,
    &.widget_nav_menu {

        ul {

            li {
                margin: 0;
                padding: 4px 0;

                &:last-child {
                    border: none;
                }

                ul {
                    margin: 4px 0 0;
                    padding: 0 0 0 10px;

                    li {

                        &:last-child {
                            padding: 4px 0 0;
                        }
                    }
                }
            }
        }
    }

    &.widget_recent_entries {
	
	    .qodef-widget-title {
            margin: 0 0 23px;
        }

        ul {

            li {
                margin: 0 0 12px;

                > span {
                    display: block;
	                font-size: 14px;
                }
            }
        }
    }

    &.widget_archive,
    &.widget_categories,
    &.widget_text {

        select {
            width: 100%;
            max-width: 100%;
            height: 54px;
            padding: 0 18px;
            line-height: 54px;
            color: $default-text-color;
            background-color: transparent;
            border: 1px solid $default-border-color;
            font-family: inherit;
            font-size: inherit;
            font-style: inherit;
            font-weight: inherit;
            outline: none;
            box-sizing: border-box;
        }
    }

    &.widget_text {

        .textwidget {

            a {
                color: $default-text-color;

                &:hover {
                    color: $default-heading-color;
                }
            }
        }

        .qodef-sf-text-holder {

            .qodef-sf-text {
                color: $default-heading-color;

                &:hover {
                    color: #969696;
                }
            }
        }
    }

    &.posts_holder {

        ul {
            
            li {

                &.page_item_has_children {

                    & > a {
                        display: block;
                    }

                    & > ul.children {
                        padding-left: 10px;
                    }
                }

                &.menu-item-has-children {

                    & > a {
                        display: block;
                    }

                    & > ul.sub-menu {
                        padding-left: 10px;
                    }
                }
            }
        }
    }

    &.widget_tag_cloud {

        .qodef-widget-title {
            margin: 0 0 12px;
        }

        a {
            position: relative;
            display: inline-block;
            vertical-align: middle;
            color: $default-text-color;
            font-family: inherit;
            line-height: 29px;
            font-weight: 400;
            font-size: 16px !important;
            background-color: transparent;
            text-decoration: underline;

            &:after {
                content: ',';
                color: $default-text-color;
                margin: 0;
            }

            &:last-child {

                &:after {
                    display: none;
                }
            }

            &:hover {
                color: $default-heading-color;
            }
        }
    }
    
    &.widget_media_image {
        margin-bottom: 20px;
        
        a, img {
            display: block;
        }
    }

    &.woocommerce.widget_product_categories,
    &.woocommerce.widget_layered_nav.woocommerce-widget-layered-nav {

        .qodef-widget-title {
            margin: 0 0 11px;
        }

        ul.product-categories {

            li {
                margin: 0;

                a {
                    font-size: 16px;
                    line-height: 36px;
                    color: $default-text-color;

                    &:hover {
                        color: $dark-gray-hover-color;
                    }
                }
            }
        }
    }

    &.woocommerce.widget_layered_nav.woocommerce-widget-layered-nav {

        ul.woocommerce-widget-layered-nav-list {

            li {
                margin: 0;

                a {
                    font-size: 16px;
                    line-height: 36px;
                    color: $default-text-color;

                    &:hover {
                        color: $dark-gray-hover-color;
                    }
                }
            }
        }
    }

    &.woocommerce.widget_price_filter {

        form {
            width: 250px;

            .price_slider_wrapper {

                div.ui-widget-content {
                    height: 3px;
                    background-color: $default-heading-color;

                    div.ui-slider-range {
                        height: 3px;
                        background-color: $default-heading-color;
                    }

                    span.ui-slider-handle {
                        width: 8px;
                        height: 8px;
                        top: -3px;
                        background-color: $default-heading-color;
                    }
                }

                .price_slider_amount {

                    margin: 15px 0 0;

                    button[type='submit'] {
                        padding: 0 0 1px 0;
                        float: right;
                        border: none;
                        line-height: 15px;
                        background-color: transparent;
                        color: $default-heading-color;

                        &:hover {
                            color: rgba($default-heading-color,.8);
                        }
                    }

                    .price_label {
                        float: left;
                        font-size: 15px;
                        line-height: 15px;
                        text-decoration: none;
                        font-style: normal;
                    }
                }
            }
        }
    }

    &.woocommerce.widget_recently_viewed_products,
    &.woocommerce.widget_products {

        .qodef-widget-title {
            margin: 0 0 11px;
        }

        ul.product_list_widget {

            li {
                margin: 0 0 69px;

                a {

                    img {
                        max-width: 101px;
                    }

                    span.product-title {
                        padding: 45px 0 0 121px;

                        &:hover {
                            color: #393939;
                        }
                    }
                }

                span.woocommerce-Price-amount {
                    margin: 0 0 0 121px;
                    font-size: 15px;
                }

                .star-rating {
                    display: none;
                }
            }
        }
    }
}

.vc_wp_search {

    .widget {

        &.widget_search {

            form {

                > div {
                    background-color: $default-background-color;
                    border: none;
                }
            }

            input {
                width: calc(100% - 66px);
                padding: 0 0 0 33px;
                font-size: 15px;
            }

            button {
                width: 66px;
                padding: 0 39px 0 9px;
            }
        }
    }
}

.qodef-page-header,
.qodef-top-bar,
.qodef-sticky-header,
.qodef-mobile-header {
    
    .widget {
        margin: 0;
        
        &.widget_pages,
        &.widget_archive,
        &.widget_categories,
        &.widget_meta,
        &.widget_recent_entries,
        &.widget_recent_comments,
        &.widget_rss,
        &.widget_calendar,
        &.widget_rss,
        &.widget_archive,
        &.widget_categories,
        &.widget_tag_cloud,
        &.widget_nav_menu,
        &.widget_search {
            display: none !important;
        }
    }
}

.qodef-side-menu {

    .widget {
	    color: #b1b2b2;
	
	    &:last-child {
		    margin: 0 !important;
	    }
	    
