                        color: inherit;
                    }

                    &[type='submit'] {
                        color: #b1b2b2;
                    }
                }

                button {

                    &:hover {
                        color: $first-main-color;
                    }
                }
            }

            &.widget_archive,
            &.widget_categories,
            &.widget_text {

                select {
                    color: #b1b2b2;
                    border-color: #b1b2b2;

